.page {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__info-area {
  color: #060b35;
  flex-direction: column;
  margin: 40px 0 0;
  padding: 0;
  display: flex;
}

.card__title {
  letter-spacing: 0;
  text-align: left;
  justify-content: left;
  margin: 0 0 4px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  letter-spacing: 0;
  text-align: left;
  color: #616070;
  justify-content: left;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review-area {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__review-text {
  letter-spacing: 0;
  text-align: right;
  font-size: 10px;
  font-weight: 400;
}

.card__price-area {
  letter-spacing: 0;
  text-align: center;
  justify-content: space-between;
  margin-top: 24px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__price-title {
  letter-spacing: 0;
  text-align: left;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: inline;
}

.card__price-value {
  letter-spacing: 0;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: inline;
}

.card__button {
  color: #fff;
  box-sizing: border-box;
  letter-spacing: 0;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .5s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

